import { Component, ElementRef, Inject, OnInit, ViewChild } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { CommonFacade } from '@core/facades/common.facade';
import { SnackService } from '@core/services/snack.service';
import { ButtonType } from '@shared/components/button/button.component';

@Component({
    selector: 'findqo-ad-share-dialog',
    templateUrl: './ad-share-dialog.component.html',
    styleUrls: ['./ad-share-dialog.component.scss'],
    standalone: false
})
export class AdShareDialogComponent implements OnInit {
	constructor(
		private snackBarService: SnackService,
		private commonFacade: CommonFacade,
		@Inject(MAT_DIALOG_DATA) public data: string,
		private matDialogRef: MatDialogRef<AdShareDialogComponent>
	) {}

	@ViewChild('adLink') adLink!: ElementRef<HTMLInputElement>;
	readonly buttonType = ButtonType;

	copyAdLink(adLink: string): void {
		const TRANSLATIONS = this.commonFacade.getTranslations();
		navigator.clipboard.writeText(adLink);

		this.snackBarService.open({
			data: { message: TRANSLATIONS['n227'], type: 'success' },
			panelClass: 'snackbar-success',
		});

		this.matDialogRef.close();
	}

	ngAfterViewInit(): void {
		this.adLink.nativeElement.value = this.data['adLink'];
	}

	ngOnInit(): void {}
}
