import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, FormsModule, ReactiveFormsModule, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogModule, MatDialogRef } from '@angular/material/dialog';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { CommonItem, ReportAd } from '@core/models';
import { ButtonType } from '@shared/components/button/button.component';
import { ButtonModule } from '@shared/components/button/button.module';
import { SharedAdsFacade } from '@shared/facades/shared-ads.facade';
import { SharedModule } from '@shared/shared.module';
import { Observable } from 'rxjs';

@Component({
    imports: [SharedModule, ButtonModule, MatInputModule, MatSelectModule, FormsModule, ReactiveFormsModule, MatDialogModule],
    selector: 'findqo-ad-report-dialog',
    templateUrl: './ad-report-dialog.component.html',
    styleUrls: ['./ad-report-dialog.component.scss'],
    providers: [SharedAdsFacade]
})
export class AdReportDialogComponent implements OnInit {
	constructor(
		@Inject(MAT_DIALOG_DATA) private data: { adId: string },
		private fb: FormBuilder,
		private sharedAdsFacade: SharedAdsFacade,
		private matDialogRef: MatDialogRef<AdReportDialogComponent>
	) {
		this.reportReasons$ = this.sharedAdsFacade.getReportAdReasons$();
	}

	readonly buttonType = ButtonType;

	form = this.fb.group({
		reportReasonId: ['', Validators.required],
		message: ['', Validators.maxLength(500)],
	});

	reportReasons$: Observable<CommonItem[]>;

	get reportMessageCount(): number {
		const count = 500 - (this.form?.get('message').value?.length || 0);

		if (count <= 0) {
			return 0;
		} else {
			return count;
		}
	}

	reportAd(): void {
		this.form.markAllAsTouched();

		if (this.form.valid) {
			const BODY: ReportAd = {
				reportReasonId: this.form.get('reportReasonId').value,
				message: this.form.get('message')?.value,
			};

			this.sharedAdsFacade.reportAd(this.data?.adId, BODY, this.matDialogRef);
		}
	}

	ngOnInit(): void {
		this.sharedAdsFacade.loadReportAdReasons();
	}
}
